* {
   box-sizing: border-box;
}
body {
   margin: 0;
   font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
   background-color: black;
}

code {
   font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.container {
   display: flex;
   justify-content: space-between;
}

.alluser {
   width: 50%;
}

.datails-edit {
   width: 50%;
   text-align: center;
}

.datails-edit div.details {
   padding-bottom: 20px;
   border-bottom: 1px solid #bbb;
   margin-bottom: 10px;
}

.adduser {
   text-align: center;
   border-top: 1px solid #bbb;
   margin: 35px auto 0;
   padding: 10px 0 20px;
   width: 70%;
}

ul.users {
   list-style: none;
   padding: 0;
}

ul.users li {
   list-style: none;
   text-align: center;
   margin: 20px;
   display: flex;
   justify-content: space-between;
   width: 80%;
}

button:last-of-type {
   margin-left: 10px;
}

form {
   margin: 0 auto;
   padding: 0;
   width: 70%;
   overflow: hidden;
   color: red;
}

form label {
   font-size: 14px;
   color: darkgray;
   cursor: pointer;
}

form label,
form input {
   float: left;
   clear: both;
}

form input {
   margin: 5px 0 10px;
   padding: 9px 11px;
   width: 100%;
   outline: none;
   border: 1px solid #bbb;
   border-radius: 9px;
   display: inline-block;
   transition: 0.2s ease all;
}

ul.users {
   color: #fff;
}

.details {
   color: wheat;
}

.details h3 {
   color: #fff;
}

.details + div {
   color: azure !important;
}
